:root {
  --whiteText: #fcfcfc;
  --blackText: #131313;
  --grayText: #ababab;
  --darkText: #7a7a7a;
  --yellowText: #f8d24e;
  --mainBG: #84a178;
  --whiteBG: #fff;
  --modalBG: #f5f5f5;
  --modalBG2: #fcfcfc;
  --ice-cream: #aec6a5;
  --ice-coffee: #efd478;
  --milkshakes: #d6936d;
  --tintIcecream: rgba(156, 190, 143, .7);
  --tintIcecoffee: rgba(248, 210, 78, .7);
  --tintMilkshake: rgba(213, 117, 63, .7);
  --formBG: rgba(248, 210, 78, .1);
  --modalGreen: #9cbe8f;
  --BTN: #f8d24e;
  --accentBTN: #0a8817;
  --disabledBTN: #ababab;
  --Gap: 16px;
  --cubic: cubic-bezier(.4, 0, .2, 1);
  --duration: 250ms;
}

body {
  letter-spacing: .03em;
  color: var(--blackText);
  font-family: Montserrat, sans-serif;
  font-size: 14px;
}

h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  margin: 0;
  display: block;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
}

.list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.link {
  color: inherit;
  text-decoration: none;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  padding-left: 16px;
  padding-right: 16px;
}

@media screen and (min-width: 480px) {
